"use scrict";

export default {
	name: "MySelect",
	data()ã€€{
		return {
			index: 0,
			multiIndex: [0, 0, 0]
		}
	},
	model: {
		prop: "value",
		event: "change"
	},
	props: {
		value: {
			type: [Number, String, Object, Array],
			defatul() {
				return "";
			}
		},
		list: {
			type: [Array, Object],
			default() {
				return [];
			}
		},
		itemLabel: {
			type: String,
			default: "label"
		},
		itemValue: {
			type: String,
			default: "value"
		},
		placeholder: {
			type: String,
			default: ""
		},
		mode: {
			type: String,
			default: "selector"
		},
		formatLabel: {
			type: Function,
			default() {
				return res => res && res.join('/');
			}
		},
		formatValue: {
			type: Function,
			default() {
				return res => Array.isArray(res) && res.map(item => item.value)
			}
		}
	},
	computed: {
		bindMode() {
			switch(this.mode) {
				case "region": return "multiSelector";
				default: return this.mode;
			}
		},
		bindIndex() {
			switch(this.mode) {
				case "selector": return this.index;
				case "region": return this.multiIndex;
			}
		},
		bindValue() {
			switch(this.mode) {
				case "selector": 
					let item = this.list.find(item => item[this.itemValue] === this.value);
					return item && item[this.itemLabel];
				case "region": return this.formatLabel(this.value);
				case "time": return this.value;
				case "date": return this.value;
			}
		},
		bindList() {
			switch(this.mode) {
				case "selector": return this._getLabel(this.list);
				case "region":
					let item = this._getAreaByIndex(this.list["86"], this.multiIndex[0]);
					let subItem = this._getAreaByIndex(this.list[item.label], this.multiIndex[1]);
					return [
						this._getLabel(this.list["86"]),
						this._getLabel(this.list[item.label]),
						this._getLabel(this.list[subItem.label])
					];
			}
		}
	},
	methods: {
		change: function (e) {
			let index = e.target.value;
			switch(this.mode) {
				case "selector":
					this.$emit("change", this.list[index][this.itemValue]);
					break;
				case "region": 
					let item = this._getAreaByIndex(this.list["86"], this.multiIndex[0]);
					let subItem = this._getAreaByIndex(this.list[item.label], this.multiIndex[1]);
					let grandsonItem = this._getAreaByIndex(this.list[subItem.label], this.multiIndex[2]);
					this.$emit("change", this.formatValue([item, subItem, grandsonItem]));
					break;
				case "time":
					this.$emit("change", e.detail.value);
					break;
				case "date":
					this.$emit("change", e.detail.value);
					break;
			}
		},
		multiChange: function (e) {
			this.$set(this.multiIndex, e.detail.column, e.detail.value);
		},
		_getLabel: function (list) {
			switch(this.mode) {
				case "selector": 
					return Array.isArray(list) ? list.map(item => item[this.itemLabel]) : [];
				case "region": 
					return Object.keys(list).map(item => list[item]);
			}
		},
		_getAreaByIndex: function (list, index) {
			let label = Object.keys(list)[index];
			return {label, value: list[Object.keys(list)[index]]};
		}
	}
}
